<template>
<div class="app-warpper">
    <!--  -->
     <maintop :id="id"/> 
     <!--  -->
    <div class="main overview">
      <keep-alive>
       <router-view  />  <!--  keep-alive-->
      </keep-alive>  
    </div>

  <!--  
  <ScaleDown width="300"  >
      <router-link to="/index/mainview">   
        <span></span>
      </router-link>
      <router-link to="/index/village"> 
        <span></span>
      </router-link>
      <router-link to="/index/Iotview"> 
        <span></span>
      </router-link>
  </ScaleDown>  
  -->
  
</div>
</template>

<script>
import { ScaleDown } from 'vue-burger-menu'
import mainview from '../broad/broadview'
import maintop from "../../components/main/maintop";
export default {
  name: 'index',
  components: {ScaleDown,mainview,maintop},//
  props:['id'] // 
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.app-warpper{
  height:100%;
}
.app-warpper /deep/ .bm-burger-bars {
    background-color: #ffffff;
  }
.app-warpper /deep/ .bm-burger-button {
    top:25px;
  }
</style>



