import Vue from 'vue'
import Router from 'vue-router'
import index from '@/page/index/index'
import login from '@/page/index/login2'//
Vue.use(Router)

export default new Router({
  //base:'/sxdataV/',//tomcat
  routes: [
    
    {                     //url
      path: '/',
      name: 'login',
      component: login
    },
    {
      path: '/index',    //url
      name: 'index',
      redirect: '/index/mainview', //mainview
      component: index,
      props: (route)=>{ //
        return {
        id: route.query.id
        }
      },
      children: [
        {       
          path: 'villageindex',  //url
          name: 'villageindex',
          component: () =>
              import (  '@/page/village/villageindex')  //
      },{       
        path: 'villageview',  //url
        name: 'villageview',
        component: () =>
            import (  '@/page/village/villageview')  //
      },  {
          path: 'mainview', //url
          name: 'mainview',
          component: () =>
              import (  '@/page/broad/broadview')
      }, {
        path: 'Iotview',  //url
        name: 'Iotmain',
        component: () =>
            import (  '@/page/iot/iotview')
    }]
    },
    
  ]
})



