<template>
    <div class="app-header">
				<el-menu :default-active="pid" class="el-menu-demo" mode="horizontal" background-color="#001931" text-color="#fff" active-text-color="#409EFF">
            <el-menu-item index="1" @click="open('/index/mainview')" >
                  <span slot="title"></span>
            </el-menu-item>
          <el-submenu index="2">
            <template slot="title"></template>
              <el-menu-item index="2-1" @click="open('/index/villageindex')">
                <span slot="title"> </span>
              </el-menu-item>
              <el-menu-item index="2-2" @click="open('/index/villageview')">
                <span slot="title"> </span>
              </el-menu-item>
          </el-submenu>
            <el-menu-item index="3" @click="open('/index/Iotview')">
                  <span slot="title"></span>
            </el-menu-item>
        </el-menu>
				<div class="heading"></div>
				<div class="bar">
         <!-- <el-tooltip effect="dark" :content="isdisplay?'':''"  placement="bottom">
            <div class="top-bar__item">
              <i @click="handledisplay">
                <img height="20" width="20" src='../../static/img/display.png' alt="" />
              </i>
            </div>
          </el-tooltip>-->
          <el-tooltip effect="dark" :content="isFullScren?'':''" placement="bottom">
            <div class="top-bar_item">
              <i :class="isFullScren?'icon-tuichuquanping':'icon-quanping'"
                @click="handleScreen">
                <img height="20" width="20" src='../../static/img/quanping.png' alt="" />
              </i>
            </div>
          </el-tooltip>
          <el-tooltip effect="dark" content="" style="font-size: 25px;" placement="bottom">
            <div class="top-bar__item top-bar__item--show">
              <mapTheme></mapTheme>
            </div>
          </el-tooltip>
         
					<i><img  src='../../static/img/uesrname.png' alt="" /></i>
					<span></span> <em>|</em><span></span>
					<button class="logout_btn" ><router-link to="/"></router-link></button>
				</div>
    </div>
</template>
<script>
import mapTheme from "./map-theme";
import { mapGetters } from 'vuex';
import { fullscreenToggel, listenfullscreen } from "@/util/util";
let moment = require("moment");//

export default {
  name: 'maintop',
  data () {
    return {
      ndata:{year:'',day:'',week:''},
      pid:this.id+"" //
    }
  },
  props:['id'],
  components: {mapTheme},//
  methods:{
    login:function(){
        window.location.href="http://110.53.162.165:5050/"
    },
    getTime(){
      let week = ['','','','','','','']//
      this.ndata.year = moment().format(" YYYY-MM-DD")//
      this.ndata.day = moment().format("HH:mm:ss")
      this.ndata.week = week[moment().format("d")]//week
    },
    LopTime(){
      //setInterval(this.getTime,1000)   //getTime
    },
    handleScreen () { //
      fullscreenToggel();
    },
    setScreen () {
      this.$store.commit("SET_FULLSCREN");
    },
    handledisplay(){
      this.$store.commit("SET_DISPLAY");
    },
    open (menupath) {

      this.$router.push(menupath)
    }

  },
 
  computed:{
    ...mapGetters(["isFullScren"]),
    ...mapGetters(["isdisplay"]),
  },
  mounted:function(){//
      //this.LopTime();
      listenfullscreen(this.setScreen);
  }
};


</script>

<style scoped>

</style>





