<template>  
    <div class="m_center2">     
        <div class="m_center">
          <!-- -->
          <v-distpicker @selected="onSelected" :province="temp.address__province" :city="temp.address__city" :area="temp.address__dist" ></v-distpicker>
          <button class="clearall-btn" v-on:click="clear"> </button>
          <button class="clearall-btn" type="primary"  v-on:click="addallspot"> </button>
        </div> 
        <baidu-map class="bm-view" :center="center" :zoom="zoom" @ready="handler" :scroll-wheel-zoom="true" :mapStyle="bmapStyle" >
           <!-- -->
          <bml-marker-clusterer :averageCenter="true">
                <bm-marker v-for="(marker,index) of markers" :key="index"  :icon="markerIcon(marker.type)" :position="{lng: marker.lng, lat: marker.lat}" v-on:click="Opencontent(marker)">
                </bm-marker>
          </bml-marker-clusterer>     
           <!---->
          <bm-map-type :map-types="['BMAP_NORMAL_MAP','BMAP_SATELLITE_MAP', 'BMAP_HYBRID_MAP']"  anchor="BMAP_ANCHOR_TOP_LEFT"></bm-map-type>
          <!-- -->
          <bm-info-window :position="{lng: infowindow.lng, lat: infowindow.lat}" :show="infowindow.show" @close="infoWindowClose" @open="infoWindowOpen">
            <ul style="margin:0 0 5px 0;padding:0.2em 0"> 
                <li><span>IMEI: </span>{{infowindow.tid}} </li>
                <li><span>: </span>{{infowindow.type}} </li>
                <li><span>: </span>{{infowindow.content}} </li>
                <li><span>: </span>{{infowindow.ctime}} </li>
            </ul>
          </bm-info-window>>
          <bm-info-window :position="{lng: normalinfowindow.lng, lat: normalinfowindow.lat}" :show="normalinfowindow.show" @close="normalinfoWindowClose" @open="normalinfoWindowOpen">
            <ul style="margin:0 0 5px 0;padding:0.2em 0"> 
                <li><span>IMEI: </span>{{normalinfowindow.tid}} </li>
                <li><span>: </span>{{normalinfowindow.aname}} </li>
                <li><span>: </span>{{normalinfowindow.uname}} </li>
                <li><span>: </span>{{normalinfowindow.phone}} </li>
                <li><span>: </span>{{normalinfowindow.address}} </li>
                <li><span>: </span>{{normalinfowindow.isuse}} </li>
            </ul>
          </bm-info-window>>
          <bm-boundary name="" :strokeWeight="2" strokeColor="blue" fillColor=""  ></bm-boundary>
        </baidu-map>
    </div> 
</template>

<script>
import VDistpicker from '@/components/main/Distpicker' //
import { mapGetters } from 'vuex';
import mapStyle from '@/static/json/mapstyle.json';//json
import {BmlMarkerClusterer} from 'vue-baidu-map'
export default {

components: { VDistpicker,BmlMarkerClusterer },//
name: "mainmap",
  data () {//
    return {
      temp: {
        address__province: '',//
        address__city: '',///
        address__dist: '',///
      },
      center: {lng: 0, lat: 0},  //
      zoom: 12,//
      infowindow:{lng:'',lat:'',tid:'',type:'',content:'',ctime:'',show:false},//
      normalinfowindow:{lng:'',lat:'',tid:'',address:'',isuse:'',uname:'',aname:'',phone:'',show:false},//
      bmapStyle: {
        styleJson:mapStyle        //
      },
      markers:[],//json
    }
  },
   methods: {//
      handler ({BMap, map}) { //
      this.center.lng = 113.195699      //
      this.center.lat = 28.240956       //
      this.zoom = 13                    //
    },
    onSelected(data) { //
      this.temp.address__province = data.province.value,
      this.temp.address__city = data.city.value,
      this.temp.address__dist = data.area.value,

      this.$store.commit('SET_SELECTPLACE',this.temp.address__dist[0]); //  
      this.center.lng = this.temp.address__dist[1],
      this.center.lat = this.temp.address__dist[2],
      this.zoom = 16
    },
    
    clear:function(event){ //
      this.temp.address__province=''
      this.temp.address__city = ''
      this.temp.address__dist = ''
      
    },
    addallspot:function(){
      const loading=this.$loading({
        lock:true,
        text:'...',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
      if(this.get()==true){       //
          setTimeout(() => {
          loading.close();
        }, 2000);
      }
     
    },
    get:function(){ //3000
            //get
            this.$http.get('http://110.53.162.165:5050/api/gps/all').then(function(res){
                          
                          for (let i = 0; i < res.data.data.length; i++) {
                            const position = {
                              tid: res.data.data[i].tid,
                              lng: res.data.data[i].longitude, 
                              lat: res.data.data[i].latitude,
                              address: res.data.data[i].address,
                              isuse: res.data.data[i].isuse,
                              uname: res.data.data[i].uname,
                              phone: res.data.data[i].phone,
                              aname: res.data.data[i].aname
                              }
                            this.markers.push(position)
                          }
                        },function(){
                    console.log('');
                });
            return true;
    },
    getrspot:function(){ //100
            //get
            this.$http.get('http://110.53.162.165:5050/api/gps/random').then(function(res){
                          
                          for (let i = 0; i < res.data.data.length; i++) {
                            const position = {
                              tid: res.data.data[i].tid,
                              lng: res.data.data[i].longitude, 
                              lat: res.data.data[i].latitude,
                              address: res.data.data[i].address,
                              isuse: res.data.data[i].isuse,
                              uname: res.data.data[i].uname,
                              phone: res.data.data[i].phone,
                              aname: res.data.data[i].aname
                              }
                            this.markers.push(position)
                          }
                        },function(){
                    console.log('');
                });
    },
    infoWindowClose:function () { //
      this.infowindow.show = false
    },
    infoWindowOpen:function () { //
      this.infowindow.show = true
    },
    normalinfoWindowClose:function () { //
      this.normalinfowindow.show=false
    },
    normalinfoWindowOpen:function () { //
      this.normalinfowindow.show=true
    },
    Opencontent:function (message) {//
      if(message.type==""){
        this.infowindow={
        lng:message.lng,
        lat:message.lat,
        tid:message.tid,
        type:message.type,
        content:message.content,
        ctime:message.ctime,
        show:true}
      }else{
        this.normalinfowindow={
          lng: message.lng, 
          lat: message.lat,
          tid: message.tid,
          address: message.address,
          isuse: message.isuse==true?"":"",
          uname: message.uname,
          phone: message.phone,
          aname: message.aname,
          show:true
        }
      }
      
    },
    // isActive:function(judge){ //v-bind  :animation="isActive(marker.type)"
    //     if(judge=="bad") //judage==warning/danger
    //     return 'BMAP_ANIMATION_BOUNCE';
    // },
    markerIcon:function(type){ //v-bind 
        if(type=="") //judage==warning/danger
        {
          const icon={url:"http://api.map.baidu.com/img/markers.png", size: {width: 23, height: 25},opts:{imageOffset:{width:0,height:-250}}};    // 
          return icon;
        }
    },
    listenzdinfo(){
        for (let i = 0; i < this.zdinfo.length; i++) {  //
          const position = {lng: this.zdinfo[i].lo, lat: this.zdinfo[i].la,type:this.zdinfo[i].type,tid:this.zdinfo[i].tid,content:this.zdinfo[i].content,ctime:this.zdinfo[i].ctime}
          this.markers.push(position)
        }
     }
    
   },
  computed: { // 
     ...mapGetters(["themeName"]),  //
     ...mapGetters(["zdinfo"]),     //
     ...mapGetters(["zdinfocontent"]),  //
     listenstage(){ //themeName    java
       return this.themeName;
     },
     listenzdinfocontent(){ //zdinfocontent
       return this.zdinfocontent;
     }
     
  },
  watch:{ //
      listenstage:function(vag){//  vagcomputedlistenstage
        this.bmapStyle={styleJson:vag}
      },
      listenzdinfocontent:function(message){ //
        this.infowindow={
          lng:message.lo,
          lat:message.la,
          tid:message.tid,
          type:message.type,
          content:message.content,
          ctime:message.ctime,
          show:true
          };
        this.center.lng = message.lo;
        this.center.lat = message.la;
        this.zoom = 15;
      }
  },
   mounted:function(){//
        this.getrspot();
        this.listenzdinfo();
    }

}
</script>
<style lang="scss" >
.bm-view {
  width: 100%;
  height: 55vh;

}
.m_center2{
  position: relative;
  overflow: hidden;
  z-index:0;
    li{
    line-height: 26px;
    font-size: 15px;
    span{
      width: 100px;display: inline-block;
    }
  }
}

.clearall-btn{
    font-size: 12px;
    height: 24px;
    border: #1968ab solid 1px;
    border-radius: 15px;
    padding: 1px 15px;
    color: #ffffff;
    background: #1968ab;
    margin: 0;
    text-align: center;
    
}
.anchorBL{
display:none;
}

</style>



